.page-footer {
  color: $grey-200;

  h2 {
    margin-top: 0;
    margin-bottom: 25px;
    margin-left: 2px;

    font-family: $roboto-condensed;
    font-size: 15px;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0.05em;
    text-transform: uppercase;

    color: $black;
  }

  a {
    font-size: 15px;
    line-height: 25px;
    color: $grey-200;
  }
}

.page-footer__wrapper {
  @include container-desktop-wide;

  display: flex;
  justify-content: space-between;
  padding-top: 65px;
  padding-bottom: 35px;

  @media (max-width: $tablet-width) {
    flex-direction: column;
    justify-content: start;
    padding: 0;
  }
}

.page-footer__block-social {
  p {
    display: none;
  }

  @media (max-width: $tablet-width) {
    @include container-desktop-wide;

    display: flex;
    align-items: center;
    justify-content: space-between;

    padding-top: 18px;
    padding-bottom: 18px;

    p {
      display: block;
      margin-left: 14px;

      font-size: 13px;
      line-height: 24px;
      letter-spacing: 0.04em;
      text-transform: uppercase;
    }
  }
}

.page-footer__block-nav {
  display: flex;
  justify-content: space-between;
  width: 70%;
  margin-left: auto;

  @media (max-width: $tablet-width) {
    @include container-desktop-wide;

    width: 100%;
    margin-left: 0;
    padding-top: 37px;
    padding-bottom: 37px;
    padding-right: 36px;

    border-top: 1px solid $grey-300;
    border-bottom: 1px solid $grey-300;
  }
}

.page-footer__toggle {
  display: none;
}

.page-footer__block-copyrigh {
  display: flex;
  justify-content: space-between;

  padding-top: 20px;
  padding-bottom: 20px;

  letter-spacing: -0.04em;
  text-transform: uppercase;

  p {
    margin: 0;
  }

  @media (max-width: $tablet-width) {
    @include container-desktop-wide;

    padding-top: 20px;
    padding-bottom: 20px;

    p {
      display: none;
    }
  }
}
