.social {
  @include list-reset;

  display: flex;
  margin-top: 45px;

  @media (max-width: $tablet-width) {
    margin: 0;
  }
}

.social__item {
  margin-right: 30px;

  &:last-child {
    margin-right: 0;
  }

  @media (max-width: $tablet-width) {
    margin-top: -10px;
    margin-right: 14px;
  }
}

.social__item a {
  display: flex;
}
