  display: flex;
  animation: bounce 0.6s; }

@keyframes bounce {
  0% {
    top: -500px; }
  100% {
    top: 50%; } }

.modal__close {
  transition: opacity 0.3s;
  position: absolute;
  top: 20px;
  right: 20px;
  width: 17px;
  height: 17px;
  background: 0 0;
  border: none;
  cursor: pointer; }
  .modal__close:hover, .modal__close:focus {
    outline: none;
    opacity: 0.7; }
  .modal__close:active {
    opacity: 0.5; }
  .modal__close::before, .modal__close::after {
    content: "";
    position: absolute;
    left: 50%;
    top: -25%;
    width: 1px;
    height: 23px;
    background-color: #ffffff;
    transform: rotate(45deg); }
  .modal__close::after {
    transform: rotate(-45deg); }
